'use strict';

import core from "jspm_packages/github/alloyui/core@master/core";
import SoyComponent from "jspm_packages/github/alloyui/core@master/soy/SoyComponent";

class Component extends SoyComponent {
  constructor(opt_config) {
    super(opt_config);
  }

  syncVisible(visible) {
    this.element.style.display = visible ? null : 'none';
  }
}

Component.ATTRS = {
  visible: {
    validator: core.isBoolean,
    value: true
  }
};

export default Component;
